import React from 'react';
import '../../App.css';
import './Shop.css';
import CustomFooter from '../footer/Footer';

function Shop() {

    return(
        <div className="App">
            <header>
                <img src="./images/punky-2020-banner.png" className="banner-img" alt="PUNKY HOLLOW 2020"/>
                <h1 className="shop-h1">Shop</h1>
            </header>
            <div class="store-items-container">
                <div class="row">
                    <div class="store-item">
                        <img src="./images/shop/mug.png" className="store-item-img" alt="store mug"/>
                    </div>
                    <div class="store-item">
                        <img src="./images/shop/mens-t.png" className="store-item-img" alt="mens t"/>
                    </div>
                    <div class="store-item">
                        <img src="./images/shop/womens-t.png" className="store-item-img" alt="womens t"/>
                    </div>
                    <div class="store-item">
                        <img src="./images/shop/long-sleeve-t.png" className="store-item-img" alt="long sleeve t"/>
                    </div>
                </div>
            </div>
            <footer>
                <CustomFooter />
            </footer>
        </div>
    );
}

export default Shop;