import React, { useState } from 'react';
import '../../App.css';
import './About.css';
import AboutDisplay from './AboutDisplay';
import CustomFooter from '../footer/Footer';
import ScrollAnimate from 'react-animate-on-scroll';

function About() {

    return(
        <div className="App">
            <header>
                <img src="./images/punky-2020-banner.png" className="banner-img" alt="PUNKY HOLLOW 2020"/>
            </header>
            <AboutDisplay />
            <ScrollAnimate animateIn="fadeIn">
                <img className="background-img" src="./images/punky-sign-cropped-more.png" alt="BEAUTIFUL PUNKY HOLLOW"/>
            </ScrollAnimate>
            <ScrollAnimate animateIn="fadeIn">
                <CustomFooter />
            </ScrollAnimate>
        </div>
    );
}

export default About;